.App {
  text-align: center;
}

.clock-container {
  position: relative;
  width: 256px;
  height: 256px;
  border-radius: 50%;
  border: 4px solid black;
  margin: 0 auto;
  background-color: #282c34;
}

.number {
  position: absolute;
  transform: translate(-50%, -50%);
  font-size: calc(8px + 2vmin);
  color: white;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
